html {
  box-sizing: border-box;
}

body {
  color: #212529;
  background-color: #f0f2f5;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 500;
}

*, :after, :before {
  box-sizing: inherit;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  display: block;
}

.container {
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

.list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.link {
  color: inherit;
  text-decoration: underline;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.button {
  cursor: pointer;
  border: 1px solid rgba(0, 0, 0, 0);
  display: inline-block;
}

.hidden {
  display: none;
}

header {
  height: 80px;
  width: 100%;
  color: #212529;
  background-color: #155aaf;
  justify-content: center;
  align-items: center;
  font-family: inherit;
  line-height: 1.5;
  display: flex;
  position: fixed;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1), 0 8px 16px rgba(0, 0, 0, .1);
}

.input {
  width: 300px;
  background-color: #f7faff;
  border: .5px solid #d3d3d3;
  border-radius: 6px;
  padding: 6px 12px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.submit-btn {
  cursor: pointer;
  background-color: #f7faff;
  border: .5px solid #d3d3d3;
  border-radius: 6px;
  margin: 0 auto;
  padding: 6px 12px;
  transition: box-shadow .25s cubic-bezier(.4, 0, .2, 1), all;
  display: inline-block;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.submit-btn:-webkit-any(:hover, :focus) {
  box-shadow: 2px 8px 8px rgba(0, 0, 0, .35);
}

.submit-btn:is(:hover, :focus) {
  box-shadow: 2px 8px 8px rgba(0, 0, 0, .35);
}

section {
  padding-top: 100px;
  padding-bottom: 60px;
}

.js-list {
  flex-wrap: wrap;
  justify-content: start;
  gap: 16px;
  display: flex;
}

.card {
  width: 24%;
  background-color: #f7faff;
  border: .5px solid #d3d3d3;
  border-radius: 6px;
  flex-direction: column;
  justify-content: space-between;
  display: flex;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.card-thumb img {
  border-radius: 6px 6px 0 0;
}

.card-descr {
  overflow-y: hidden;
}

.img-info {
  flex-direction: row;
  justify-content: space-between;
  padding: 8px 12px;
  display: flex;
}

.img-info li {
  flex-direction: column;
  display: flex;
}

.img-info li p {
  text-align: center;
  padding-bottom: 4px;
  font-size: 12px;
  font-weight: bold;
}

.img-info li span {
  text-align: center;
  font-size: 12px;
  font-weight: 400;
}

.js-load {
  cursor: pointer;
  background-color: #f7faff;
  border: .5px solid #f7faff;
  border-radius: 6px;
  margin: 20px auto;
  padding: 6px 12px;
  transition: box-shadow .25s cubic-bezier(.4, 0, .2, 1), background-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1), border .25s cubic-bezier(.4, 0, .2, 1), all;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.js-load:-webkit-any(:hover, :focus) {
  color: #f7faff;
  background-color: #155aaf;
  border: .5px solid #155aaf;
  box-shadow: 2px 8px 8px rgba(0, 0, 0, .35);
}

.js-load:is(:hover, :focus) {
  color: #f7faff;
  background-color: #155aaf;
  border: .5px solid #155aaf;
  box-shadow: 2px 8px 8px rgba(0, 0, 0, .35);
}

footer {
  height: 40px;
  width: 100%;
  color: #f7faff;
  background-color: #155aaf;
  justify-content: center;
  align-items: center;
  padding: 4px;
  font-family: inherit;
  line-height: 1.5;
  display: flex;
  position: fixed;
  bottom: 0;
  left: 0;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1), 0 8px 16px rgba(0, 0, 0, .1);
}

